#myBtn {
    display: none;
    position: fixed; 
    bottom: 20px; 
    right: 30px; 
    z-index: 99; 
    border: none; 
    outline: none; 
    background-color: $secondary-background-color; 
    color: $accent-color; 
    cursor: pointer; 
    padding: 5px; 
    border-radius: 50%; 
    font-size: 18px; 
    border: 2px solid $yellow-color;
}

#myBtn:hover {
    background-color: $yellow-color; 
}
.icon-up {
    width: 25px;
    height: 22px;
}